*{
  font-family: Georgia, 'Times New Roman', Times, serif;
  box-sizing: content-box;
}

body{
  padding: 60px;
}

input{
  font-size: 1.2rem;
  color: grey;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin: 10px;
  width: 350px;
  height: auto;
}

.card p{
  padding: 3px;
  text-align: center;
}

.card img {
  width: 100%;
  height: 300px;
  background-size: contain;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}